import * as types from './types';

let reducer = (state,action)=>{
    let {type,payload}=action;
    switch(type){
        case types.VIEW_NAV:
            return Object.assign({},state,{
                bNav:payload
            });
        case types.VIEW_FOOT:
            return Object.assign({},state,{
                bFoot:payload
            });
        case types.VIEW_LOADING:
            return Object.assign({},state,{
                bLoading:payload
            });
        case types.UPDATE_SLIST:
            return Object.assign({},state,{
                sList:payload
            });
        case types.CLEAR_SLIST:
            return Object.assign({},state,{
                sList:[]
            });
        case types.UPDATE_HOME:
            return Object.assign({},state,{
                home:payload
            });
        case types.UPDATE_FIND:
            return Object.assign({},state,{
                find:payload
            });
        case types.UPDATE_DETAIL:
            return Object.assign({},state,{
                detail:payload
            });
        case types.UPDATE_USER:
            return Object.assign({},state,{
                user:{
                    auth:true,
                    userData:payload
                }
            });

            
        case types.ADD_ITEM:

            let arr=[...state.buycar];
            let find=false;
            arr.forEach((item,index)=>{
            if(item.id==payload.id){
               item.number++;//数量递增
                find=true;
            }
            });
            if(!find) {
            payload.number=1;//数量
            arr.push(payload);
            }
            
            return Object.assign({},state,{
                buycar:arr
            });


        case types.CHANGE_ITEM:
            state.buycar.forEach((item,index)=>{
                if(item.id==payload.id){
                    if(payload.num==-1){
                        if(item.number==1){
                            item.number=1;
                            alert('受不了了，宝贝不能再少了~')
                        }else{
                            item.number+=payload.num;
                        }
                    }else{
                        item.number+=payload.num;
                    }
                }
            });
            return Object.assign({},state,{
                buycar:[...state.buycar]
            });

        case types.REMOVE_ITEM:
            state.buycar.forEach((item,index)=>{
                if(item.id==payload.id){
                    state.buycar.splice(index,1);
                }
            });
            return Object.assign({},state,{
                buycar:[...state.buycar]
            });


        case types.CLEAR_ITEM:
            return Object.assign({},state,{
                buycar:[]
            });

            
        default:
            return state;  
    }
};

export default reducer;