import React, { Component } from 'react';

import {Route,Switch,Redirect} from 'react-router-dom';

import {connect} from 'react-redux';
import * as types from '../store/types';

import AuthUser from "../guard/AuthUser";


import Header from '../common/Header';
import Footer from '../common/Footer';

import Home from './Home';
import Classy from './Classy';
import Find from './Find';
import Car from './Car';
import Detail from './Detail';
import User from './User';
import Login from './Login';
import Reg from './Reg';
import Myset from './Myset';
import ErrorPage from '../common/Error';
import Loading from '../common/Loading';

class App extends Component {
  componentWillReceiveProps(nextProps){
    if(this.props.location.pathname !== nextProps.location.pathname){
      window.scrollTo(0,0)
      let routerPathName=nextProps.location.pathname;
      if(/home|classy/.test(routerPathName)){
        this.props.viewNav(true);
        this.props.viewFoot(true);
      }
      if(/find|user/.test(routerPathName)){
        this.props.viewNav(false);
        this.props.viewFoot(true);
      }
      if(/car|login|reg|myset|detail/.test(routerPathName)){
        this.props.viewNav(false);
        this.props.viewFoot(false);
      }
    }

  }
  render() {
    return (
      <>
        {this.props.bLoading && <Loading/>}
        {this.props.bNav && <Header/>}
        {/* <Header/> */}
        <Switch>
            <Route path='/home' component={Home}></Route>
            <Route path='/classy' component={Classy}></Route>
            <Route path='/find' component={Find}></Route>


            {/* <Route path='/car' component={Car}></Route> */}

             <AuthUser
                path="/car"
                component={Car}
              />


            {/* <Route path='/user' component={User}></Route> */}

              <AuthUser
                path="/user"
                component={User}
              />

            <Route path='/login' component={Login}></Route>
            <Route path='/reg' component={Reg}></Route>
            <Route path='/detail/:id' component={Detail}></Route>
            <Route path='/myset' component={Myset}></Route>
            <Redirect exact from='/' to='/home'></Redirect>
            <Route component={ErrorPage}></Route>
        </Switch>
        {this.props.bFoot && <Footer/>}
        
      </>
    )
  }
}

let mapStateToProps=(state)=>{
  return {
    bLoading:state.bLoading,
    bNav:state.bNav,
    bFoot:state.bFoot
  };
}

let mapDispatchToProps= (dispatch)=>({
  viewNav:(bl)=>{dispatch({type:types.VIEW_NAV,payload:bl})},
  viewFoot:(bl)=>{dispatch({type:types.VIEW_FOOT,payload:bl})}
})


export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(App);