import React from 'react';
import QueueAnim from "rc-queue-anim";

import {Link} from 'react-router-dom';
import querystring from 'query-string';

let Blist = ({list,dataName}) => {
  return <section className="aui-list-product">
	<QueueAnim 
		className="aui-list-product-box"
		type='top'
        duration='1000'
	>
		{
			list.map((item,index)=>(
				<Link className="aui-list-product-item" key={item.id}
					to={{
						pathname:'/detail/'+item.id,
						search:querystring.stringify({
						dataName:dataName
						})}
					}
				>
					<div className="aui-list-product-item-img">
						<img src={item.src} />
					</div>
					<div className="aui-list-product-item-text">
						<h3>{item.name}</h3>
						<div className="aui-list-product-mes-box">
						<div>
							<span className="aui-list-product-item-price">
							<em>¥</em>
							{item.price}
							</span>
							<span className="aui-list-product-item-del-price">
							¥{item.oldprice}
							</span>
						</div>
						<div className="aui-comment">{item.comment}</div>
						</div>
					</div>
				</Link>
			))
		}
	
	</QueueAnim>
	</section>

};
export default Blist;