import React from 'react';

import QueueAnim from "rc-queue-anim";


let Slist = ({sList}) => {
  // console.log(sList)
  if(!sList[0]){
    return null
  }
  return  <div className="aui-list-content">
      <div className="aui-list-item">
        <div className="aui-list-item-img">
          <img src="/img/pd/cf-4.jpg" />
        </div>
        <div className="aui-slide-box">
          <div className="aui-slide-list">
            <QueueAnim 
              component="ul"   
              className="aui-slide-item-list"
              type='top'
              duration='1000'
            >
              {
                sList[0].list1.map((item,index)=>(
                  <li className="aui-slide-item-item" key={item.id}>
                    <a href="javascript:;" className="v-link">
                      <img className="v-img" src={item.src} />
                      <p className="aui-slide-item-title aui-slide-item-f-els">{item.name}</p>
                      <p className="aui-slide-item-info">
                        <span className="aui-slide-item-price">{item.price}</span>&nbsp;&nbsp;<span className="aui-slide-item-mrk">{item.oldprice}</span>
                      </p>
                    </a>
                  </li>
                ))
              }
            </QueueAnim>
          </div>
        </div>
      </div>
      <div className="aui-list-item">
        <div className="aui-list-item-img">
          <img src="/img/pd/cf-5.jpg" />
        </div>
        <div className="aui-slide-box">
          <div className="aui-slide-list">
            <QueueAnim component="ul" className="aui-slide-item-list">
              {
                sList[0].list2.map((item,index)=>(
                  <li className="aui-slide-item-item" key={item.id}>
                    <a href="javascript:;" className="v-link">
                      <img className="v-img" src={item.src} />
                      <p className="aui-slide-item-title aui-slide-item-f-els">{item.name}</p>
                      <p className="aui-slide-item-info">
                        <span className="aui-slide-item-price">{item.price}</span>&nbsp;&nbsp;<span className="aui-slide-item-mrk">{item.oldprice}</span>
                      </p>
                    </a>
                  </li>
                ))
              }
            </QueueAnim>
          </div>
        </div>
      </div>
      <div className="aui-list-item">
        <div className="aui-list-item-img">
          <img src="/img/pd/cf-7.jpg" />
        </div>
        <div className="aui-slide-box">
          <div className="aui-slide-list">
            <QueueAnim component="ul" className="aui-slide-item-list">
              {
                  sList[0].list3.map((item,index)=>(
                    <li className="aui-slide-item-item" key={item.id}>
                      <a href="javascript:;" className="v-link">
                        <img className="v-img" src={item.src} />
                        <p className="aui-slide-item-title aui-slide-item-f-els">{item.name}</p>
                        <p className="aui-slide-item-info">
                          <span className="aui-slide-item-price">{item.price}</span>&nbsp;&nbsp;<span className="aui-slide-item-mrk">{item.oldprice}</span>
                        </p>
                      </a>
                    </li>
                  ))
                }
            </QueueAnim>
          </div>
        </div>
      </div>
      <div className="aui-list-item">
        <div className="aui-list-item-img">
          <img src="/img/pd/cf-6.jpg" />
        </div>
        <div className="aui-slide-box">
          <div className="aui-slide-list">
            <QueueAnim component="ul" className="aui-slide-item-list">
              {
                sList[0].list4.map((item,index)=>(
                  <li className="aui-slide-item-item" key={item.id}>
                    <a href="javascript:;" className="v-link">
                      <img className="v-img" src={item.src} />
                      <p className="aui-slide-item-title aui-slide-item-f-els">{item.name}</p>
                      <p className="aui-slide-item-info">
                        <span className="aui-slide-item-price">{item.price}</span>&nbsp;&nbsp;<span className="aui-slide-item-mrk">{item.oldprice}</span>
                      </p>
                    </a>
                  </li>
                ))
              }
            </QueueAnim>
          </div>
        </div>
      </div>

    </div>

};
export default Slist;