export  let asyncActionAuth =(url,type,username,password,props)=>{
    
  return (dispatch)=>{
    let params = new URLSearchParams();
    params.append('username',username);
    params.append('password',password);
    params.append('act','login');
    dispatch({type:types.VIEW_LOADING ,payload:true});
      fetch(
           url,
          {
            method:'post',
            body:params
          }
        ).then(
          res=>res.json()
        ).then(
          data=>{
            dispatch({type:types.VIEW_LOADING ,payload:false})
            // console.log(data)
            if(data.msg=='登录成功'){
              alert(data.msg)
              dispatch({type:type,payload:data.info});
              props.history.push('/user')
            }else{
              alert(data.msg)
            }
          }
        )
    }
  }
