import React from 'react';

let Header = (props) => (
  <header className="aui-header-default aui-header-fixed aui-header-bg">
	<a href="#" className="aui-header-item">
		<i className="aui-icon aui-icon-code" />
	</a>
	<div className="aui-header-center aui-header-center-clear">
		<div className="aui-header-search-box">
		<i className="aui-icon aui-icon-small-search" />
		<input type="text" placeholder="手机箱包" className="aui-header-search" />
		</div>
	</div>
	<a href="#" className="aui-header-item-icon">
		<i className="aui-icon aui-icon-packet" />
		<i className="aui-icon aui-icon-member" />
	</a>
   </header>

);
export default Header;