import React, { Component } from 'react';

import ReactSwipe from 'react-swipe';

import {connect} from 'react-redux';
import * as types from '../store/types';

import {asyncActionSList,asyncActionBList} from '../store/asyncAction';


import Slist from '../common/Slist';
import Blist from '../common/Blist';

import './Find.css';


class Find extends Component {
  componentDidMount(){
    this.props.getlist();
    this.props.getfind()
  }
  render() {
    return (
      <>
        <header className="aui-header-default aui-header-fixed aui-header-bg">
          <a href="#" className="aui-header-item">
            <i className="aui-icon aui-icon-code" />
          </a>
          <div className="aui-header-center aui-header-center-clear">
            <div className="aui-header-center-logo">
              <div><img src="/img/user/icon-dis.png"  /></div>
            </div>
          </div>
          <a href="#" className="aui-header-item-icon">
            <i className="aui-icon aui-icon-packet" />
            <i className="aui-icon aui-icon-member" />
          </a>
        </header>
        <section className="n-tabs">
          <ul className="n-tabContainer" id="auto-id-1509603311057">
            <li className="n-tabItem" data-id="homepage">
              <a href="#" id="homepage" className="current">精选</a>
            </li>
            <li className="n-tabItem" data-id="44114">
              <a href="#" id="44114">直播</a>
            </li>
            <li className="n-tabItem" data-id="15394">
              <a href="#" id="15394">视频购</a>
            </li>
            <li className="n-tabItem" data-id="01436">
              <a href="#" id="01436">社区</a>
            </li>
            <li className="n-tabItem" data-id="18211">
              <a href="#" id="18211">家居生活</a>
            </li>
            <li className="n-tabItem" data-id="83651">
              <a href="#" id="83651">生鲜</a>
            </li>
            <li className="n-tabItem" data-id="37957">
              <a href="#" id="37957">数码</a>
            </li>
            <li className="n-tabItem" data-id="74029">
              <a href="#" id="74029">个人洗护</a>
            </li>
            <li className="n-tabItem" data-id="73179">
              <a href="#" id="73179">海外直邮</a>
            </li>
            <li className="n-tabItem" data-id="41804">
              <a href="#" id="41804">服饰鞋靴</a>
            </li>
          </ul>
        </section>
        <div className="aui-banner-content aui-fixed-top" data-aui-slider>
          <ReactSwipe className="aui-banner-wrapper find-wrapper" swipeOptions={{
                continuous: true,
                speed:500,
                auto:1000,
            }}>
            <div className="aui-banner-wrapper-item">
              <a href="#">
                <img src="/img/banner/find-2.jpg" />
              </a>
            </div>
            <div className="aui-banner-wrapper-item">
              <a href="#">
                <img src="/img/banner/find-1.jpg" />
              </a>
            </div>
          </ReactSwipe>
          <div className="aui-banner-pagination">
          </div>

          <Slist sList={this.props.sList}></Slist>

          <div className="aui-recommend">
            <img src="/img/bg/icon-tj2.jpg"  />
          </div>

          <Blist list={this.props.find1} dataName='find'></Blist>

        </div>



          

      </>
    )
  }
}

let mapStateToProps=(state)=>(
  {
   find1:state.find,
   sList:state.sList
  }
)

let mapDispatchToProps= (dispatch)=>({
  getlist:()=>{asyncActionSList(
    'http://localhost:3000/list_content',
    dispatch,
    types.UPDATE_SLIST
  )},
  getfind:()=>{asyncActionBList(
    'http://localhost:3000/list_detail',
    dispatch,
    types.UPDATE_FIND
  )}
})


export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Find);