import React, { Component } from 'react';

import {connect} from 'react-redux';
import * as types from '../store/types';

import {Link} from 'react-router-dom';


class Car extends Component {
  render() {
    let allPrice = 0;
    let sum = 0;
    this.props.buycar.map((item,index)=>{
      sum += item.number;

      allPrice += parseFloat(item.price) * item.number;
      })
      allPrice=allPrice.toFixed(2);
    return (
      <>
        <header className="aui-header-default aui-header-fixed aui-header-bg">
          <Link to='/home' className="aui-header-item">
            <i className="aui-icon aui-icon-back-white" />
          </Link>
          <div className="aui-header-center aui-header-center-clear">
            <div className="aui-header-center-logo">
              <div className="aui-car-white-Typeface">购物车({sum})</div>
            </div>
          </div>
          <a href="javascript:;" className="aui-header-item-icon" style={{minWidth: 0}}>
            <i className="aui-icon aui-icon-member" />
          </a>
        </header>
        <section className="aui-car-content">
          <div className="aui-car-box">
            <div className="aui-car-box-name">
              <input type="checkbox" className="check goods-check shopCheck" />
              <h3>
                <a href="javascript:;">够味达精选网</a>
              </h3>
              <div className="aui-car-coupons">
                <a href="javascript:;">领券</a>
                <a href="javascript:;">编辑</a>
              </div>
            </div>
            <div className="aui-car-box-list">
              <ul>

                {
                  this.props.buycar.map((item,index)=>(
                    <li key={item.id}>
                      <div className="aui-car-box-list-item">
                        <input type="checkbox" className="check goods-check goodsCheck" />
                        <div className="aui-car-box-list-img">
                          <Link to={{pathname:'/detail/'+item.id}}>
                            <img src={item.img1} />
                          </Link>
                        </div>
                        <div className="aui-car-box-list-text">
                          <h4>
                            <a href="javascript:;">{item.name}</a>
                          </h4>
                          <div className="aui-car-box-list-text-brief">
                            <span>哈哈哈</span>
                            <span>嘻嘻嘻</span>
                            <span>呵呵呵</span>
                          </div>
                          <div className="aui-car-box-list-text-price">
                            <div className="aui-car-box-list-text-pri">
                              ￥<b className="price">{item.price}</b>
                            </div>
                            <div className="aui-car-box-list-text-arithmetic">
                              <a href="javascript:;" className="minus" 
                                  onClick={this.props.changeItem.bind(this,{id:item.id,num:-1})}
                              >-</a>
                              <span className="num">{item.number}</span>
                              <a href="javascript:;" className="plus"
                                  onClick={this.props.changeItem.bind(this,{id:item.id,num:1})}
                              >+</a>
                            </div>
                          </div>
                        </div>
                      <input 
                          type="button" 
                          className='car-del-item' 
                          value='删除'
                          onClick={this.props.removeItem.bind(this,{id:item.id})}
                      />
                      </div>
                    </li>
  
                  ))
                }

              </ul>
            </div>
            <div className="aui-shopPrice">
              本店总计：￥
              <span className="aui-total-amount ShopTotal">{allPrice}</span>
              <br/>
             <input type="button" value="清空" onClick={this.props.clearItem.bind(this)}/>
            </div>
          </div>
        </section>
        <div className="aui-payment-bar">
          <div className="all-checkbox"><input type="checkbox" className="check goods-check" id="AllCheck" />全选</div>
          <div className="shop-total">
            <strong>合计：<i className="total" id="AllTotal">{allPrice}</i></strong>
          </div>
          <a href="order.html" className="settlement">结算</a>
        </div>
      </>
    )
  }
}

let mapStateToProps=(state)=>({
  buycar:state.buycar,
});

let mapDispatchToProps=(dispatch)=>({
  changeItem:(info)=>{
    dispatch({
      type:types.CHANGE_ITEM,
      payload:info
    })},
  removeItem:(info)=>{
    dispatch({
              type:types.REMOVE_ITEM,
              payload:info
          })},
  clearItem:()=>{
    dispatch({
              type:types.CLEAR_ITEM,
          })}
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Car);