import React from 'react';

import {NavLink} from'react-router-dom';


let Footer = (props) => (
  <footer className="aui-footer-default aui-footer-fixed">
	<NavLink to='/home' activeClassName='aui-footer-active' className="aui-footer-item">
		<span className="aui-footer-item-icon aui-icon aui-footer-icon-home" />
		<span className="aui-footer-item-text">首页</span>
	</NavLink>
	<NavLink to='/classy' activeClassName='aui-footer-active' className="aui-footer-item">
		<span className="aui-footer-item-icon aui-icon aui-footer-icon-class" />
		<span className="aui-footer-item-text">分类</span>
	</NavLink>
	<NavLink  to='/find' activeClassName='aui-footer-active' className="aui-footer-item">
		<span className="aui-footer-item-icon aui-icon aui-footer-icon-find" />
		<span className="aui-footer-item-text">发现</span>
	</NavLink>
	<NavLink  to='/car' activeClassName='aui-footer-active' className="aui-footer-item">
		<span className="aui-footer-item-icon aui-icon aui-footer-icon-car" />
		<span className="aui-footer-item-text">购物车</span>
	</NavLink>
	<NavLink  to='/user' activeClassName='aui-footer-active' className="aui-footer-item">
		<span className="aui-footer-item-icon aui-icon aui-footer-icon-me" />
		<span className="aui-footer-item-text">我的</span>
	</NavLink>
   </footer>

);
export default Footer;