import React, { Component } from 'react';

import {connect} from 'react-redux';
import * as types from '../store/types';


import Banner from './Banner';
import Slist from '../common/Slist';
import Blist from '../common/Blist';
import {asyncActionSList,asyncActionBList} from '../store/asyncAction';

class Home extends Component {
  componentDidMount(){
    this.props.gethome();
    this.props.getlist();
  }
  render() {
    return (
        <>
          <Banner></Banner>

          <section className="aui-grid-content">
            <div className="aui-grid-row">
              <a href="javascript:;" className="aui-grid-row-item">
                <i className="aui-icon-large aui-icon-sign" />
                <p className="aui-grid-row-label">每日签到</p>
              </a>
              <a href="javascript:;" className="aui-grid-row-item">
                <i className="aui-icon-large aui-icon-time" />
                <p className="aui-grid-row-label">限时抢购</p>
              </a>
              <a href="my-memjavascript:;" className="aui-grid-row-item">
                <i className="aui-icon-large aui-icon-vip" />
                <p className="aui-grid-row-label">会员专享</p>
              </a>
              <a href="my-pjavascript:;" className="aui-grid-row-item">
                <i className="aui-icon-large aui-icon-group" />
                <p className="aui-grid-row-label">好货拼团</p>
              </a>
              <a href="my-invjavascript:;" className="aui-grid-row-item">
                <i className="aui-icon-large aui-icon-share" />
                <p className="aui-grid-row-label">分享领券</p>
              </a>
            </div>
          </section>

       
          <Slist sList={this.props.sList}></Slist>

         <div className="aui-recommend">
          <img src="/img/bg/icon-tj1.jpg" />
        </div>


          <Blist list={this.props.home} dataName='home'></Blist>
       
        </>
    )
  }
}


let mapStateToProps=(state)=>{
  // console.log(state.sList)
  return {
    home:state.home,
    sList:state.sList
  };
}

let mapDispatchToProps= (dispatch)=>({
  getlist:()=>{asyncActionSList(
    // '/data/home/list_content.json',
    'http://localhost:3000/list_content',
    dispatch,
    types.UPDATE_SLIST
  )},
  gethome:()=>{asyncActionBList(
    // '/data/home/list_detail.json',
    'http://localhost:3000/list_detail',
    dispatch,
    types.UPDATE_HOME
  )}
})


export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Home);