import React, { Component } from 'react';

import ReactSwipe from 'react-swipe';


class Banner extends Component {
  render() {
    return (
        <div className="aui-banner-content aui-fixed-top" data-aui-slider>
            <ReactSwipe className="aui-banner-wrapper"  swipeOptions={{
                continuous: true,
                speed:500,
                auto:1000,
            }}>
                <div className="aui-banner-wrapper-item">
                <a href="#">
                    <img src="/img/banner/banner.jpg" />
                </a>
                </div>
                <div className="aui-banner-wrapper-item">
                <a href="#">
                    <img src="/img/banner/banner1.jpg" />
                </a>
                </div>
                <div className="aui-banner-wrapper-item">
                <a href="#">
                    <img src="/img/banner/banner2.jpg" />
                </a>
                </div>
                <div className="aui-banner-wrapper-item">
                <a href="#">
                    <img src="/img/banner/banner3.jpg" />
                </a>
                </div>
            </ReactSwipe>
            <div className="aui-banner-pagination" />
        </div>

    )
  }
}
export default Banner;
