import React, { Component } from 'react';

import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import {asyncActionAuth} from '../store/asyncAction';
import * as types from '../store/types';

class Login extends Component {
  state={
    username:'',
    password:''
  }
  render() {
    return (
     <div>
        <div className="top1 ">
          <div className="top2 ">
            <a href="javascript:window.history.go(-1) "><span className="top3">返回</span></a> 
          </div>
          <div className="top4 ">登录</div>
        </div>
        <div className="div_w "/>
        <section className="form-group" id="inputObj1">
          <div className="form-text-login noa" id="accounts_1">
            <label className="font-two">用户名：</label>

            <input 
                type="text" 
                name="username"  
                id="username"  
                className="form-text" 
                placeholder="手机号/用户名"
                value={this.state.username}
                onChange={this.changeIpt.bind(this)}
            />
          
          </div>
          <div className="form-text-login nob" id="pwd_1">
            <label className="font-two">密　码：</label>

            <input 
                type="password" 
                name="password"  
                id="password"  
                className="form-text"
                placeholder="请输入密码"
                value={this.state.password}
                onChange={this.changeIpt.bind(this)}
             />
            
            <a href="javascript:void(0);" className="form-text-clear" id="txtPhoneB"><i className="r passbg" id="togglePassword" /></a>
          </div>
          <div className="btn5" style={{marginTop: 12}}>
            <input 
              type="button" 
              id="SubmitLoginBtn"
              defaultValue="登 录" 
              onClick={
                this.log.bind(this)
              } 
            /> 
            <Link to='/reg'>
              <span style={{float: 'left', fontSize: 13, color: '#ff5500'}}>注册新帐号</span>
            </Link>
            <span style={{float: 'right', fontSize: 13, color: '#ff5500'}}>忘记密码?</span>
          </div>
        </section>
      </div>

    )
  }

  changeIpt(ev){
    this.setState({
      [ev.target.name]:ev.target.value
    })
  }

  log(){
    this.props.login(this.state.username,this.state.password,this.props)
    // username.value=password.value='';
    document.querySelector("#username").value=document.querySelector("#password").value='';
    this.state.username=this.state.password='';
  }

}

let mapStateToProps=(state)=>{
  return {
    auth:state.user.auth,
    userData:state.user.userData
  };
}

let mapDispatchToProps= (dispatch)=>({
  login:(username,password,props)=>{
    dispatch(asyncActionAuth(
    'http://localhost:3000/login',
    types.UPDATE_USER,
    username,
    password,
    props
  ))}
})


export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Login);