import React, { Component } from 'react';


class Reg extends Component {
  state={
    username:'',
    password:''
  }
  render() {
    return (
     <div>
      <div className="top1 ">
        <div className="top2 ">
          <a href="Javascript:history.go(-1) "><span className="top3 ">返回</span></a> 
        </div>
        <div className="top4 ">注册</div>
      </div>
      <div className="div_w " />
      <section className="form-group" id="inputObj1">
        <div className="form-text-logins noas" id="accounts_1">
          <label className="font-two">  &nbsp;手机号：</label>
          <input 
            name="username" 
            id="username" 
            style={{border: 'none', marginLeft: '5%', height: '80%', width: '60%'}}
            placeholder="请输入您的手机号"
            value={this.state.username}
            onChange={this.changeIpt.bind(this)}
          />
        </div>
        <div className="form-text-login nob" id="accounts_1">
          <label className="font-two">密　码：</label>
          <input 
            type="password" 
            id="password" 
            name="password" 
            className="form-text" 
            placeholder="请输入密码" 
            style={{marginLeft: 30}}
            value={this.state.password}
            onChange={this.changeIpt.bind(this)}
          />
          <a href="javascript:void(0);" className="form-text-clear" id="txtPhoneB"><i className="r passbg" id="togglePassword" /></a>
        </div>
        <div className="btn5" style={{marginTop: 12}}>
          <input  
            defaultValue="注册"
            onClick={
              this.reg.bind(this)
            } 
         />
        </div>
      </section>
    </div>

    )
  }

  changeIpt(ev){
    this.setState({
      [ev.target.name]:ev.target.value
    })
  }

  reg(){
    let params = new URLSearchParams();
    params.append('username',this.state.username);
    params.append('password',this.state.password);
    params.append('act','reg');
    fetch(
      `http://localhost:3000/login`,
      {
        method:'post',
        body:params
      }
    ).then(
      res=>res.json()
    ).then(
      data=>{
        if(data.msg=="用户名已存在"){
          alert(data.msg);
          // username.value=password.value='';
          document.querySelector("#username").value=document.querySelector("#password").value=''
          this.state.username=this.state.password='';
        }else{
          alert("注册成功");
          this.props.history.push('/login')
        }
      }
    )
  }

}



export default Reg;